* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    --primary-color: #3E9BD1;
    --happy-face: #FFCD00;
    --sad-face: #FF532B;
}

body {
    height: 100vh;
    width: 100%;
}

a {
    text-decoration: none;
}
.faces-container {
    display: flex;
    flex-wrap: wrap;
    margin: 10px;
}

.face {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 50px;
    border-radius: 40px;
    font-size: 20px;
    margin: 5px;
}

.happy-face {
    background-color: var(--happy-face);
}

.sad-face {
    background-color: var(--sad-face);
    color: white;
}
.add-button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    background-color: var(--primary-color);
    color: white;
    font-size: 40px;
}

.form-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 50px;
}

.hidden-message {
    display: none;
}

.form-btn {
    width: 150px;
    height: 30px;
    text-align: center;
    border: none;
    border-radius: 20px;
    font-size: 18px;
}

.accept-btn {
    background-color: var(--primary-color);
    color: white;
    position: relative;
}

.cancel-btn {
    background-color: lightgrey;
}

.back-btn {
    position: absolute;
    z-index: -1;
}


